import {Component, Input, OnInit} from '@angular/core';


@Component({
  selector: 'app-data-loader',
  templateUrl: './data-loader.component.html',
  styleUrls: ['./data-loader.component.css']
})
export class DataLoaderComponent {
  maxentry: boolean = false;

  constructor() {
    setTimeout(() => {
      this.maxentry  = true;
    },3000);
  }

}



