import {Injectable} from "@angular/core";
import {RestDatasourceService} from "./rest-datasource.servics";
import {Observable} from "rxjs/Observable";
import {Section} from "../models/section.model";
import {Project} from "../models/project.model";


@Injectable()
export class SectionRepository {
  private sections: Section[];

  constructor(private dataSource: RestDatasourceService) {
    this.dataSource.get('section').subscribe(data => {
      this.sections = data;
      console.log(this.sections);
    });
  }

  getNames() :string[]{
    return this.sections.map(_section => _section.name);
  }

  getNamesByProject(project: Project): string[] {
    return this.sections
      .filter(_section => {
        return _section.project && _section.project.id == project.id
      })
      .map(_section => _section.name);
  }

  getByProject(project: Project): Section[] {
    return this.sections.filter(_section => {
      return _section.project && _section.project.id == project.id;
    }).slice();
  }
}



