import {AfterViewInit, Component, Input, OnInit} from '@angular/core';
import {ProjectRepository} from "../../../services/project-repository.service";
import {ProjectSummary} from "../../../models/project-summary.model";
import {Project} from "../../../models/project.model";
import {SectionRepository} from "../../../services/section-repository.service";
import {DocumentRepository} from "../../../services/document-repository.service";
import {Section} from "../../../models/section.model";
import  {Document} from "../../../models/document.model";

declare var $: any;
@Component({
  selector: 'app-project-sidebar',
  templateUrl: './project-sidebar.component.html',
  styleUrls: ['./project-sidebar.component.css']
})
export class ProjectSidebarComponent implements OnInit, AfterViewInit{

  ngAfterViewInit(): void {
    $('.ui.rating')
    .rating({
      initialRating: 0,
      maxRating: 5
    });


  }

  sections: Section[];
  selectedSection: Section;
  isClose: boolean = false;

  constructor(private projectRepository: ProjectRepository,
              private sectionRepository: SectionRepository,
              private documentRepository: DocumentRepository) {

  }

  ngOnInit(): void {
    this.sections = this.sectionRepository.getByProject(this.projectRepository.currentProject);
    this.projectRepository.changeCurrentProjectEmitter.subscribe(project => {
      this.sections = this.sectionRepository.getByProject(project);
    })
  }

  getCountDocuments(sectionId: number) {
    return this.getDocumentBySection(sectionId).length;
  }

  getDocumentBySection(sectionId: number): Document[] {
    return this.documentRepository.getBySectionId(sectionId);
  }
}



