import {Component, Inject, OnInit} from '@angular/core';
import {ActivatedRoute, Router, UrlSegment} from "@angular/router";
import {Observer} from "rxjs/Observer";
import {Page, PAGE_STATE, PageState} from "../../models/page-state.model";

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.css']
})
export class LocationComponent {
  private urlSegments: string[];
  private prevUrlSegments: string[];
  private url: string;

  constructor(private route: ActivatedRoute,
              private router: Router,
              @Inject(PAGE_STATE) private pageStateObserver: Observer<PageState>) {
    this.toSegments(this.router.url);
    this.changePageState();

    this.router.events.subscribe(event => {
      if (event['url'] != null) {
        let prevUrl = this.url;
        this.url = event['url'];
        this.toSegments(this.url);
        if (prevUrl != this.url) {
          this.changePageState();
        }
      }
    });
  }

  private toSegments(url: string) {
    this.prevUrlSegments = this.urlSegments;
    this.urlSegments = [];
    let _urls: string[] = url.split('/');
    this.urlSegments = _urls.filter(url => {
      return url != null && url.length != 0 && url.trim() != ""
    });
  }

  private tourl(index: number): string {
    let _urls: string = "";
    for (let i = 0; i < index + 1; i++) {
      _urls += "/" + this.urlSegments[i];
    }
    return _urls;
  }

  changePageState() {
    if(this.urlSegments.length == 0 || this.urlSegments[0] == ''){
      this.pageStateObserver.next(new PageState(Page.HOME, this.url));
      return;
    }
    if(this.prevUrlSegments != null && this.urlSegments[0] == this.prevUrlSegments[0]) {
      return;
    }
    switch (this.urlSegments[0]) {
      case 'home':
        this.pageStateObserver.next(new PageState(Page.HOME, this.url));
        break;
      case 'project':
        console.log('project');
        this.pageStateObserver.next(new PageState(Page.PROJECT, this.url));
        break;
      case 'section':
        console.log('section');
        this.pageStateObserver.next(new PageState(Page.SECTION, this.url));
        break;
      case 'document':
        console.log('document');
        this.pageStateObserver.next(new PageState(Page.DOCUMENT, this.url));
        break;
    }
  };

}



