import {Inject, Injectable} from "@angular/core";
import {Http, Request, RequestMethod} from "@angular/http";
import {Observable} from "rxjs/Observable";
import 'rxjs/Rx';
import {REST_URL_TOKEN} from "../app.tokens";


@Injectable()
export class RestDatasourceService {
  backendUrl: string;

  constructor(private http: Http,
              @Inject(REST_URL_TOKEN) private url: string) {
    this.backendUrl = url;
    console.log(this.backendUrl);
  }

  get(url: string, params?: {param:string,value: string}[]): Observable<any> {
    let queryParam :URLSearchParams = null;
    if(params) {
      for(let p of params) {
        queryParam.set(p.param, p.value);
      }
    }
    return this.request(RequestMethod.Get, url, queryParam);
  }

  post(url: string, body: any): Observable<any> {
    return this.request(RequestMethod.Post, url, null, body);
  }

  put(url: string, body: any): Observable<any> {
    return this.request(RequestMethod.Put, url, null, body);
  }

  delete(url: string): Observable<any> {
    return this.request(RequestMethod.Delete, url);
  }


  request(method: RequestMethod, url: string, params?: URLSearchParams, body?: any): Observable<any> {
    let req: Request = new Request({
      method: method,
      url: this.backendUrl + url,
      params: params,
      body: body
    });
    return this.http.request(req).map(res => res.json()).catch((error: Response) => {
      console.log(error);
      return Observable.throw({status:error.status, code:error.statusText});
    })
  }
}



