import {NgModule} from "@angular/core";
import { FooterComponent } from './footer/footer.component';
import { BottomComponent } from './bottom/bottom.component';
import { HeaderComponent } from './header/header.component';
import {CommonModule} from "@angular/common";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import { LocationComponent } from './location/location.component';
import { MainComponent } from './main/main.component';
import {
  MatButtonModule, MatDialogModule, MatFormFieldModule, MatIconModule, MatInputModule, MatMenuModule,
  MatSelectModule, MatToolbarModule
} from "@angular/material";
import {RouterModule} from "@angular/router";
import {ListChapterComponent} from "./components/list-chapter/list-chapter.component";
import {ListChapterItemComponent} from "./components/list-chapter/list-chapter-item/list-chapter-item.component";
import { DataLoaderComponent } from './components/data-loader/data-loader.component';
import { AboutPageComponent } from './about-page/about-page.component';
import { DisplayDateComponent } from './components/display-date/display-date.component';
import {MainFirstGuard} from "../guards/home-first.guard";
import { ErrorDialogComponent } from './components/error-dialog/error-dialog.component';

@NgModule({
  declarations: [
    FooterComponent,
    BottomComponent,
    HeaderComponent,
    LocationComponent,
    MainComponent,
    ListChapterComponent,
    ListChapterItemComponent,
    DataLoaderComponent,
    AboutPageComponent,
    DisplayDateComponent,
    ErrorDialogComponent,
  ],
  imports : [
    MatButtonModule,
    MatInputModule,
    MatFormFieldModule,
    MatIconModule,
    MatSelectModule,
    MatToolbarModule,
    MatMenuModule,
    MatDialogModule,
    CommonModule,
    BrowserAnimationsModule,
    RouterModule,
  ],
  providers: [
    MainFirstGuard,
  ],
  exports : [
    FooterComponent,
    BottomComponent,
    HeaderComponent,
    LocationComponent,
    ListChapterComponent,
    ListChapterItemComponent,
    DisplayDateComponent,
    DataLoaderComponent,
    AboutPageComponent,
    ErrorDialogComponent,
    MatInputModule,
    MatIconModule,
    MatToolbarModule,
    MatDialogModule,
    MatFormFieldModule,
    MatSelectModule,
    MatButtonModule,
    MatMenuModule,
  ]
})
export class ShareModule{ }



