import {ErrorHandler, Injectable} from "@angular/core";
import {ErrorRepository} from "./error-repository.service";
import {ErrorMessage} from "../models/error-message.model";

@Injectable()
export class ErrorMessageHandler implements ErrorHandler{

  constructor(private errorRepository: ErrorRepository){}

  handleError(error: any): void {
    console.log(error);
    setTimeout(() => {
      this.errorRepository.report(new ErrorMessage(error.status, error.code, 'en'));
    },0);
  }

}



