import {ErrorHandler, NgModule} from "@angular/core";
import {RestDatasourceService} from "./rest-datasource.servics";
import {DocumentRepository} from "./document-repository.service";
import {ProjectRepository} from "./project-repository.service";
import {SectionRepository} from "./section-repository.service";
import {StaticDatasourceService} from "./static-datasource.service";
import {Subject} from "rxjs/Subject";
import {PAGE_STATE, PageState} from "../models/page-state.model";
import {WebPageRepository} from "./webpage-repository.service";
import {ErrorMessageHandler} from "./error-message-handler";
import {ErrorRepository} from "./error-repository.service";


@NgModule({
  providers: [
    StaticDatasourceService,
    RestDatasourceService,
    /*  App Repository  */
    DocumentRepository,
    ProjectRepository,
    WebPageRepository,
    SectionRepository,
    ErrorRepository,
    {provide: ErrorHandler, useClass: ErrorMessageHandler },
    {provide: PAGE_STATE, useValue: new Subject<PageState>()}
  ],
})
export class ServiceModule{}



