import {Component, Inject, OnDestroy, OnInit } from '@angular/core';
import {Page, PAGE_STATE, PageState} from "../../models/page-state.model";
import {Observable} from "rxjs/Observable";
import {Subscription} from "rxjs/Subscription";
import {WebPageRepository} from "../../services/webpage-repository.service";
import {WebPage} from "../../models/webpage.model";
import {ActivatedRoute} from "@angular/router";
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
declare var $: any;

@Component({
  selector: 'app-about-page',
  templateUrl: './about-page.component.html',
  styleUrls: ['./about-page.component.css'],
  animations: [
    trigger('renew',[
      state('inactive', style({
        opacity: 0,
      })),
      state('active', style({
        opacity: 1,
      })),
      transition('inactive => active', animate(500)),
      transition('active => inactive', animate(500))
    ])
  ]
})
export class AboutPageComponent implements OnDestroy{
  pageStateSubscription: Subscription;
  webPage: WebPage;
  renewState = 'active';

  constructor(@Inject(PAGE_STATE) private pageStates: Observable<PageState>,
              private route: ActivatedRoute,
              private webPageRepository: WebPageRepository) {
     this.pageStateSubscription = this.pageStates
       .delay(500)
       .subscribe((pageState: PageState) => {
          this.renewContent();
          setTimeout(() =>{
            this.webPage = this.webPageRepository.getByName(Page[pageState.page]);
          }, 500)
      });
  }

  ngOnDestroy(): void {
    this.pageStateSubscription.unsubscribe();
  }

  private renewContent() {
    this.renewState = 'inactive';
    setTimeout(() => this.renewState = 'active', 500);
  }
}



