import {Injectable} from "@angular/core";
import {RestDatasourceService} from "./rest-datasource.servics";
import {Section} from "../models/section.model";
import {Document} from "../models/document.model";

@Injectable()
export class DocumentRepository {
  documents: Document[] = [];

  constructor(private dataSource: RestDatasourceService) {
      this.dataSource.get('document').subscribe(data => {
        this.documents = data;
      });

      this.dataSource.get('document/-1').subscribe(data => {});
  }


  getBySection(section: Section): Document[] {
    return this.getBySectionId(section.id);
  }

  getBySectionId(sectionId: number) {
    return this.documents
      .filter(_document => _document.section && _document.section.id == sectionId )
      .slice();
  }
}



