import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import {ShareModule} from "./shared/share.module";
import {ServiceModule} from "./services/service.module";
import {HttpModule} from "@angular/http";
import {RouterModule, Routes} from "@angular/router";
import {MainComponent} from "./shared/main/main.component";
import {DocumentModule} from "./document/document.module";
import {HEROKU_SERVER_URL, JSON_SERVER_URL, PROTOCOL_TOKEN, REST_URL_TOKEN, SPRING_SERVER_URL} from "./app.tokens";
import {ProjectModule} from "./project/project.module";
import {MainFirstGuard} from "./guards/home-first.guard";


const routes: Routes = [
  {path: '', component: MainComponent, canActivate: [MainFirstGuard] },
  {path: 'home', component: MainComponent, canActivate: [MainFirstGuard]},
];


@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    /*  App module  */
    ShareModule,
    ServiceModule,
    DocumentModule,
    ProjectModule,
    RouterModule.forRoot(routes),
    /*  Angular module  */
    HttpModule,
    BrowserModule,
  ],
  providers: [
    MainFirstGuard,
    /*  Test Server */
    {provide: REST_URL_TOKEN, useValue: HEROKU_SERVER_URL},
    /*  Deploy Server */
    /* {provide: RestDatasourceService, useClass: StaticDatasourceService}*/
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }




