import {Injectable} from "@angular/core";
import {RestDatasourceService} from "./rest-datasource.servics";
import {Observable} from "rxjs/Observable";
import {WebPage} from "../models/webpage.model";

@Injectable()
export class WebPageRepository {
  webPages: WebPage[] = [];

  constructor(private restDatasource: RestDatasourceService) {
    this.restDatasource.get('webpage').subscribe(data => {
        this.webPages = data;
    });
  }

  getByName(name: string): WebPage {
    return this.webPages.find(webPage => webPage.name.toUpperCase() == name.toUpperCase());
  }

}



